angular
  .module('app', ['ui.router']);

angular
  .module('app')
  .component('app', {
    templateUrl: 'app/hello.html'
  });

angular.module("app").run(["$templateCache", function($templateCache) {$templateCache.put("app/hello.html","<h1>Hello World!</h1>\n");}]);
angular
  .module('app')
  .config(routesConfig);

/** @ngInject */
function routesConfig($stateProvider, $urlRouterProvider, $locationProvider) {
  $locationProvider.html5Mode(true).hashPrefix('!');
  $urlRouterProvider.otherwise('/');

  $stateProvider
    .state('app', {
      url: '/',
      template: '<app></app>'
    });
}
